import React from 'react'
import { reactLocalStorage } from 'reactjs-localstorage';

const Ghostel = () => {
    const Userdata = JSON.parse(reactLocalStorage.get('userdata'))
    function showrooms()
    {
       const mainblock= document.getElementById("rids");
       mainblock.innerHTML="";
       for(var i=1;i<Math.floor(Math.random() * 20) + 5;i++)
       {
           const r=document.createElement("button");
           r.innerText="R-"+i;
           r.setAttribute("id","roomid");
           mainblock.appendChild(r);
       }
       reactLocalStorage.set(Userdata.email,"GR-1")
       alert("Room Changed!!")
    }
    return (
        <div className="hostelbox">
            <div className="hostel_header">
            <i id="ico" class="fas fa-female"></i>
            <h1>GIRL'S HOSTEL!!</h1>
            <i id="ico" class="fas fa-female"></i>
            </div>
            <div className="blocks_col">
                <button id="blkbtn" onClick={()=>showrooms()}>Block-A</button>
                <button id="blkbtn" onClick={()=>showrooms()}>Block-B</button>
                <button id="blkbtn" onClick={()=>showrooms()}>Block-C</button>
                <button id="blkbtn" onClick={()=>showrooms()}>Block-D</button>
                <button id="blkbtn" onClick={()=>showrooms()}>Block-E</button>
            </div>
            <div id="rids">
                Choose block to see available rooms!!
            </div>
        </div>
    )
}

export default Ghostel
